* {
  margin: 0;
  font-family: Roboto, sans-serif;
}

body {
  font-size: 12px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  text-align: right;
  margin-right: 20px;
}

.nav__item:last-child {
  margin: 0;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.logo {
  width: 40px;
  height: 40px;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.is-active {
  color: #00acdc;
}

